<template>
		<div id="good_List">
			<div class="container" :data:skuId="item.skuId" :data:spuId='item.spuId' v-for="(item,index) in goodList">
				<!--<router-link :to="{ name : 'GoodDetail' , query: {spuId:item.spuId,skuId:item.skuId,promId:item.promId}}">-->
					<div class="goods-img" @click="toGoodsDetails(item)">
						<img :src="item.imagePath" alt=""/>
					</div>
				<!--</router-link>-->
	
				<div class="goods-info" @click="toGoodsDetails(item)">
					<!--<router-link :to="{ name : 'GoodDetail' , query: { spuId:item.spuId,skuId:item.skuId,promId:item.promId}}">-->
						<div class="goods-name">{{item.name}}</div>
						<div class="goods-content">
							<div class="goods-price"><em class="price-icon">￥</em>{{item.discountPrice}}</div>
							<div class="goods-comments">
								<!--<span class="goods-label">{{item.tags}}</span>-->
								<span class="goods-comment">{{item.commentCount}} 条评价</span>
								<span class="goods-rate fl-right">{{item.praiseCount}} 好评</span>
							</div>
						</div>
					<!--</router-link>-->
				</div>
	
			</div>
		</div>
	</template>
	
	<script>
		import Bus from '@/bus.js'
	
		export default {
			props: ['goodList'],
			data() {
				return {
	
				}
			},
			methods: {
				toGoodsDetails(item){
					console.log(item)
					let promId = item.promId
					let skuId = item.skuId
					let spuId = item.spuId
					let type = item.type
					this.$router.push({name:'GoodDetail',query:{promId,skuId,spuId,type}})
				},
			},
		}
	</script>
	
	<style lang="scss">
		@import "../../sass/baseSass.scss";
		#good_List {
			.container {
				display: flex;
				height: 3.47rem;
				padding: 0.27rem 0.4rem;
				border-bottom: 1px solid $border-color;
				.goods-img {
					width: 2.93rem;
					height: 2.93rem;
					border-radius: 8px;
					>img {
						width: 2.93rem;
						height: 2.93rem;
						border-radius: 8px;
						
					}
				}
				.goods-info {
					position: relative;
					flex: 1;
					line-height: 1;
					margin-left: 0.35rem;
					text-align: left;
					.goods-name {
						font-size: 0.37rem;
						line-height: 1.286;
						@include textflow-two;
						color: $text-color;
					}
					.goods-content {
						position: absolute;
						width: 100%;
						bottom: 0.07rem;
					}
					.goods-price {
						font-size: 0.45rem;
						font-weight: bold;
						padding-top: 0.93rem;
						color: $price-color;
						.price-icon {
							font-size: 0.37rem;
						}
					}
					.goods-comments {
						font-size: 0.32rem;
						margin-top: 0.24rem;
						color: $text-color2;
						.goods-label {
							@include goodslabel;
						}
					}
				}
			}
		}
	</style>


