<template>
  <div id="giftDetail__good">
    <div class="good" v-for="(item,index) in gifts">
      <div class="good__img">
        <img :src="item.imagePath" alt="">
      </div>
      <div class="good__info">
        <div class="info__top">
          <div class="info__top__name">{{item.giftName}}</div>
          <div class="info__top__type">{{item.spec}} {{item.size}}</div>
        </div>
        <div class="info__price">
          <span class="price">￥<span class="price__num">{{item.price}}</span>
          </span>
          <span class="num">x{{item.num}}</span>
        </div>
      </div>
    </div>
  </div>
</template>
<script type="text/javascript">
	export default{
		props:{
			gifts:{
				type:Array,
				default:[]
			}
		},
		data(){
			return{
				
			}
		},
		
		methods:{
			
		},
	}
</script>

<style lang="scss">
  #giftDetail__good {
    margin-top: 0.2667rem;

    .good {
      display: flex;
      padding: 0.2667rem 0.4rem;
      height: 2.133rem;
      box-sizing: content-box;
      background: #fff;

      .good__img {
        height: 2.13rem;
        width: 2.13rem;
        margin-right: 0.4rem;

        img {
          height: 100%;
          width: 100%;
          border-radius: 4px;
        }
      }

      .good__info {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        flex:1;
        .info__top {
          .info__top__name {
            font-size: 0.373rem;
            color: #333;
          }
          .info__top__type {
            font-size: 0.32rem;
            color: #666;
          }
        }

        .info__price {
          display: flex;
          justify-content: space-between;

          .price {
            color: #fe4436;

            .price__num {
              font-size: 0.4267rem;
            }
          }

          .num {
            color: #727272;
          }
        }
      }
    }
  }
</style>




